<template>
    <div class="panel panel-default">
        <div class="panel-heading">
            <h4><router-link to="/" class="pull-left">返回</router-link>HdSmart.ready</h4>
        </div>
        <div class="panel-body">
            <pre class="text-left">
//该函数所封装的对应的app函数为window.onDeviceReady
HdSmart.ready(function(){
    this.messages.push('ready callback1');
});
HdSmart.ready(function(){
    this.messages.push('ready callback2');
});
            </pre>
            <div v-for="msg in messages">
                {{msg}}
            </div>
        </div>
        <div class="panel-footer">
            <button class="btn btn-default" @click="onClick">点击按钮</button>
        </div>
    </div>
</template>
<script>
    export default {
        name: "ready",
        data (){
            return {
                messages: ["等待点击"]
            }
        },
        methods:{
            onClick (){
                HdSmart.ready(()=>{
                    this.messages.push('ready callback1');
                });
                HdSmart.ready(()=>{
                    this.messages.push('ready callback2');
                });
            }
        }
    }
</script>


